@font-face {
  font-family: "Compose";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/Compose-Light.ttf") format("ttf"), url("../fonts/Compose-Light.woff") format("woff"), url("../../fonts/Compose-Light.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Compose-Regular.ttf") format("ttf"), url("../fonts/Compose-Regular.woff") format("woff"), url("../../fonts/Compose-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Compose-Medium.ttf") format("ttf"), url("../fonts/Compose-Medium.woff") format("woff"), url("../../fonts/Compose-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Compose-SemiBold.ttf") format("ttf"), url("../fonts/Compose-SemiBold.woff") format("woff"), url("../../fonts/Compose-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Compose-Bold.ttf") format("ttf"), url("../fonts/Compose-Bold.woff") format("woff"), url("../../fonts/Compose-Bold.woff2") format("woff2");
}
.svg-image-intro-arrow-up-45-white, .svg-image-arrow-white-down-45 {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow-white-down-45 {
  background-position: 100% 0;
  background-size: 260% auto;
  width: 25px;
}

.svg-image-arrow-white-down-45:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 96%;
}

.svg-image-arrow-white-down-45-dims {
  width: 25px;
  height: 24px;
}

.svg-image-intro-arrow-up-45-white {
  background-position: 0 0;
  background-size: 162.5% auto;
  width: 40px;
}

.svg-image-intro-arrow-up-45-white:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-image-intro-arrow-up-45-white-dims {
  width: 40px;
  height: 40px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

:root {
  --white-color: #fff;
  --blue-color: #00114f;
  --main-font: "Compose", sans-serif;
  --roboto: "Roboto", sans-serif;
  --transition: all 0.2s ease-out;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--main-font);
  color: var(--blue-color);
  background-color: #fff;
  min-width: 320px;
}
body.lock {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1500px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 63px;
}
@media only screen and (max-width: 1399px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 28px;
  }
}

.header.white,
.header.white .logo__main,
.header.white .menu__link,
.header.white .auth__login,
.header.white .auth__signup,
.header.white .lang__selected .lang__value {
  color: var(--white-color);
}
.header.white .auth__signup {
  border: 1px solid rgba(255, 255, 255, 0.6);
}
.header.white .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.white .menu__item::after {
  border: 1px solid var(--white-color);
}
.header.white .header__line,
.header.white .auth::after {
  background-color: var(--white-color);
}
.header.white .header__burger span, .header.white .header__burger::after, .header.white .header__burger::before {
  background-color: var(--white-color);
}

.header.dark,
.header.dark .logo__main,
.header.dark .menu__link,
.header.dark .auth__login,
.header.dark .auth__signup,
.header.dark .lang__selected .lang__value {
  color: var(--blue-color);
}
.header.dark .auth__signup {
  border: 1px solid rgba(0, 17, 79, 0.6);
}
.header.dark .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.dark .menu__item::after {
  border: 1px solid var(--blue-color);
}
.header.dark .header__line,
.header.dark .auth::after {
  background-color: var(--blue-color);
}
.header.dark .header__burger span, .header.dark .header__burger::after, .header.dark .header__burger::before {
  background-color: var(--blue-color);
}

.header {
  position: absolute;
  top: 41px;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.header__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
@media (max-width: 1280px) {
  .header__container {
    padding: 0 51px 0 30px;
  }
}
@media only screen and (max-width: 350px) {
  .header__container {
    padding: 0 20px;
  }
}

.header__navbar {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 38px;
}
@media (max-width: 1280px) {
  .header__logo {
    margin-right: 0;
  }
}

.logo {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .logo {
    display: block;
  }
}

.logo__main {
  position: relative;
  top: -5px;
  margin-right: 18px;
  font-weight: 700;
  font-size: 35px;
  line-height: 44px;
  text-decoration: none;
}

.logo__descr {
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
}

.header__line {
  margin-right: 51px;
  width: 1px;
  height: 59px;
}
@media only screen and (max-width: 1399px) {
  .header__line {
    margin-right: 35px;
  }
}
@media (max-width: 1280px) {
  .header__line {
    display: none;
  }
}

.header__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .header__nav {
    display: none;
  }
}

.header__menu {
  margin-right: 117px;
}
@media only screen and (max-width: 1399px) {
  .header__menu {
    margin-right: 60px;
  }
}

.menu__list {
  min-width: 394px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 1399px) {
  .menu__list {
    min-width: 380px;
  }
}

.menu__item {
  position: relative;
  list-style-type: none;
}
.menu__item::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.menu__item:hover::after {
  visibility: visible;
  opacity: 1;
  transform: translateY(4px);
}

.menu__link {
  font-weight: 400;
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.01em;
  text-decoration: none;
}

.lang {
  position: relative;
}
.lang .lang__shevron {
  transition: var(--transition);
}
.lang.active .lang__shevron {
  transform: scaleY(-1);
}
.lang.active .lang__selected::after {
  display: block;
}

.lang__selected {
  display: flex;
  align-items: center;
  gap: 0 12px;
  cursor: pointer;
}
.lang__selected .lang__icon img {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.lang__selected::after {
  display: none;
  content: "";
  position: absolute;
  top: 28px;
  left: -30%;
  right: -50%;
  height: 70px;
}

.lang__value {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
}

.lang__icon {
  width: 29px;
  height: 29px;
}
.lang__icon img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.lang.active .lang__dropdown {
  visibility: visible;
  opacity: 1;
}

.lang__dropdown {
  position: absolute;
  top: 67px;
  left: -32px;
  z-index: 2px;
  width: 205px;
  min-height: 228px;
  padding: 13px 13px 20px;
  background: var(--white-color);
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.lang__dropdown .lang__value {
  color: var(--blue-color);
}

.lang__item {
  display: flex;
  align-items: center;
  gap: 0 13px;
  padding: 7px 14px 5px;
  cursor: pointer;
  transition: var(--transition);
}
.lang__item .lang__icon {
  width: 35px;
  height: 35px;
}
.lang__item .lang__value {
  font-weight: 600;
  font-size: 17px;
  line-height: 37px;
  color: var(--blue-color);
}
.lang__item:hover {
  background-color: rgba(0, 17, 79, 0.1);
  border-radius: 6px;
}

.header__panel {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .header__panel {
    position: relative;
    top: -15px;
    justify-content: space-between;
    min-width: 115px;
  }
}

.auth {
  display: flex;
  align-items: flex-end;
}
@media (max-width: 1280px) {
  .auth {
    position: relative;
  }
  .auth::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 1px;
    height: 59px;
  }
}

.auth__login,
.auth__signup {
  display: flex;
  align-items: flex-end;
  height: 68px;
  margin-top: 9px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  text-decoration: none;
  transition: var(--transition);
}

.auth__login {
  margin-right: 20px;
  width: 145px;
  padding: 24px 0 18px 0;
  justify-content: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.auth__login:hover {
  background-color: var(--blue-color);
  border-radius: 6px;
}
.auth__login svg {
  display: none;
}
@media (max-width: 1280px) {
  .auth__login {
    width: auto;
    height: auto;
    margin-right: 34px;
    margin-top: 0;
  }
  .auth__login span {
    display: none;
  }
  .auth__login svg {
    display: block;
  }
  .auth__login:hover {
    background-color: transparent;
  }
}

.auth__signup {
  width: 188px;
  padding: 24px 30px 18px 41px;
}
@media (max-width: 1280px) {
  .auth__signup {
    display: none;
  }
}
.auth__signup .icon__arrow {
  display: inline-block;
  margin-left: 27px;
  transform: rotate(-45deg);
  transition: var(--transition);
}
.auth__signup:hover {
  background-color: var(--blue-color);
  border: 1px solid var(--blue-color);
  border-radius: 6px;
}
.auth__signup:hover .icon__arrow {
  transform: rotate(0);
}

.dark .auth__login:hover,
.dark .auth__signup:hover {
  color: var(--white-color);
}

.dark .auth__signup:hover path {
  fill: var(--white-color);
  stroke: var(--white-color);
}

.header__burger {
  display: none;
  position: relative;
  width: 31px;
  height: 23px;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 1280px) {
  .header__burger {
    display: flex;
  }
}
.header__burger span, .header__burger::before, .header__burger::after {
  width: 100%;
  height: 3px;
  border-radius: 20px;
  background-color: var(--white-color);
}
.header__burger span {
  display: block;
}
.header__burger::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
}
.header__burger::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 54.84%;
}

.main {
  flex-grow: 1;
}

.intro {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 700px;
  max-height: 1500px;
}
@media (max-width: 768px) {
  .intro {
    min-height: 600px;
  }
}

.intro__panel,
.intro__slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.intro__panel {
  z-index: 20;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.intro__next {
  position: relative;
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 171px 94px auto;
  border-radius: 50%;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  border: none;
}
@media (max-width: 768px) {
  .intro__next {
    display: none;
  }
}

.intro__btn {
  position: absolute;
  top: 0;
  left: 0;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid var(--white-color);
  transition: var(--transition);
  transform-origin: bottom bottom;
}
.intro__btn:hover {
  transform: scale(1.433);
}

.pagination {
  position: relative;
  display: flex;
  width: 110px;
  height: 21px;
  justify-content: space-between;
  align-items: center;
  margin: 0 21px 97px auto;
}
@media (max-width: 768px) {
  .pagination {
    margin: 0 auto 35px;
  }
}

.pagination__item {
  width: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  appearance: none;
  outline: none;
  cursor: pointer;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  transition: var(--transition);
}

.pagination__pointer {
  position: absolute;
  left: -14px;
  top: -12px;
  z-index: -1;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background-color: #042185;
}

.intro__bottom {
  position: relative;
  width: 100%;
  max-width: 1500px;
  min-height: 197px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .intro__bottom {
    min-height: 102px;
  }
}
.intro__bottom .container {
  height: 100%;
}
@media (max-width: 768px) {
  .intro__bottom .container {
    display: none;
  }
}

.intro__inner {
  height: 100%;
  display: flex;
  align-items: flex-end;
  padding-top: 20px;
  padding-bottom: 5px;
  border-top: 1px solid var(--white-color);
}

.intro__scroll {
  margin-bottom: 62px;
  text-decoration: none;
}
.intro__scroll span {
  display: block;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.01em;
  color: var(--white-color);
}
.intro__scroll span:nth-child(2) {
  margin-bottom: 25px;
  font-weight: 400;
}
.intro__scroll span:nth-child(3) {
  transition: var(--transition);
  transform-origin: top left;
}
.intro__scroll:hover span:nth-child(3) {
  transform: scale(1.9);
}

.intro__signup {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 750px;
  min-height: 176px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0 68px 0 63px;
}
.intro__signup span {
  display: inline-block;
  margin-bottom: 58px;
}
@media (max-width: 1280px) {
  .intro__signup {
    width: 680px;
  }
}
@media (max-width: 1024px) {
  .intro__signup {
    width: 550px;
  }
}
@media (max-width: 768px) {
  .intro__signup {
    width: 100%;
    min-height: 98px;
    padding: 0 34px 0 29px;
  }
  .intro__signup span {
    margin-bottom: 30px;
    width: 22px;
    height: 22px;
  }
}

.intro__link,
.intro__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.intro__link {
  z-index: 10;
}

.intro__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(277.24deg, #071b64 33.49%, #265792 104.72%);
}

.intro__sup,
.intro__text {
  font-weight: 400;
  letter-spacing: -0.01em;
  color: var(--white-color);
}

.intro__sup {
  margin-bottom: 35px;
  font-size: 10px;
  line-height: 12px;
}
@media (max-width: 768px) {
  .intro__sup {
    margin-bottom: 10px;
  }
}

.intro__text {
  margin-bottom: 53px;
  font-size: 30px;
  line-height: 38px;
}
@media (max-width: 768px) {
  .intro__text {
    margin-bottom: 25px;
    font-size: 26px;
    line-height: 32px;
  }
}

.intro__slider {
  z-index: 1;
  overflow: hidden;
}

.intro__foreground {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100vw;
  z-index: 10;
  transform: translateX(-100vw);
  background-color: #040a27;
}

.intro__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.intro__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.intro__item:nth-child(1) {
  z-index: 1;
}
.intro__item:nth-child(2) {
  z-index: 2;
}
.intro__item:nth-child(3) {
  z-index: 3;
}
.intro__item:not(:first-child) {
  left: 100%;
}
.intro__item:not(:first-child) .intro__content {
  transform: translateX(-100%);
}

.intro__content {
  width: 100%;
  height: 100%;
}

.intro__image {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}
.intro__image img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1024px) {
  .intro__image img {
    object-position: 70% center;
  }
}

.intro__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  background-color: #051034;
  opacity: 0.27;
}

.intro__about {
  position: absolute;
  left: 0;
  bottom: 238px;
  width: 100%;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media (max-width: 576px) {
  .intro__about {
    bottom: 212px;
  }
}

.intro__suptitle {
  margin-bottom: 21px;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.01em;
  color: var(--white-color);
}

.intro__title {
  margin-bottom: -8px;
  font-weight: 400;
  font-size: 100px;
  line-height: 80%;
  letter-spacing: -0.01em;
  color: var(--white-color);
}
@media (max-width: 1280px) {
  .intro__title {
    font-size: 80px;
  }
}
@media (max-width: 1024px) {
  .intro__title {
    font-size: 60px;
  }
}
@media (max-width: 768px) {
  .intro__title {
    font-size: 60px;
  }
}
@media (max-width: 576px) {
  .intro__title {
    font-size: 50px;
  }
}

footer {
  padding: 20px;
}